<template>
	<Tabs v-model="tab" class="tab">
		<Tab-pane label="清贷客户" name="outLoan" class="tab-item">
			<div class="search-box" @keyup.enter="searchoutLoan">
				<div class="search-item">
					<Input size="large" v-model="outLoanTab.searchParams.loanId" style="width: 260px"
						   placeholder="请输入贷款编号">
					<span slot="prepend" class="input-title">贷款编号</span>
					</Input>
				</div>
				<div class="search-item yellow-color">
					<Input size="large" v-model="outLoanTab.searchParams.aBaseName" style="width: 260px"
						   placeholder="请输入客户姓名">
					<span slot="prepend" class="input-title">客户姓名</span>
					</Input>
				</div>
				<div class="search-item">
					<div class="item-title color-green">放款日期</div>
					<Date-picker size="large" type="daterange" :editable="false" placement="bottom-end"
								 format="yyyy-MM-dd" placeholder="选择放款日期范围--年/月/日" style="width:352px"
								 v-model="outLoanTab.dateRange" @on-change="dateChange"
					></Date-picker>
				</div>
				<div class="search-item">
					<i-button type="primary" size="large" class="search" @click="searchoutLoan">查询</i-button>
					<i-button type="success" size="large" class="search" @click="clearAll">清空</i-button>
				</div>
			</div>
			<Table size="large" :columns="outLoanTab.outLoanHeader" :data="outLoanTab.outLoanList"></Table>
			<div class="page">
				<Page placement='top' :total="outLoanTab.totalPage" :current="outLoanTab.searchParams.currentPage"
					  :page-size="outLoanTab.searchParams.pageSize" :page-size-opts="[10,20,30,40]"
					  show-elevator show-sizer show-total
					  @on-change="pageChange" @on-page-size-change="pageSizeChange"
				></Page>
			</div>
		</Tab-pane>
		<i-button type="text" slot="extra" class="extra-btn" icon="ios-loop-strong" @click="refresh">刷新</i-button>
	</Tabs>
</template>
<script>
	export default {
		data () {
			return {
				outLoanTab: {
					outLoanHeader: [
						{
							type: 'index',
							title: '序号',
							width: '80px',
							align: 'center'
						},
						{
							title: '贷款编号',
							key: 'loanId',
							align: 'center',
							render: (h, params) => {
								return h('router-link', {
									props: {
										to: {name: 'loandetail', params: {loanId: params.row.loanId}},
									}
								}, params.row.loanId)
							}
						},
						{
							title: '贷款产品',
							key: 'eProduct',
							align: 'center'
						},
						{
							title: '业务城市',
							key: 'city',
							align: 'center'
						},
						{
							title: '客户姓名',
							key: 'name',
							align: 'center'
						},
						{
							title: '贷款金额',
							key: 'loanMoney',
							align: 'center',
							render: (h, params) => {
								if(params.row.repaymentSchedule == 'null'){
									return h('span', '-')
								}else {
									return h('span', this.fmoney(params.row.repaymentSchedule.loanAmount))
								}
							}
						},
						{
							title: '期数',
							key: 'periods',
							align: 'center'
						},
						{
							title: '还款方式',
							key: 'repayWay',
							align: 'center'
						},
						{
							title: '利率（%）',
							key: 'rate',
							align: 'center',
							render:(h,params)=>{
								return h('span',`${params.row.rate}%`)
							}
						},
						{
							title: '放款日期',
							key: 'finalTransferDate',
							align: 'center',
							render: (h, params) => {
								return h('span', new Date(params.row.finalTransferDate).Format('yyyy-MM-dd'))
							}
						},
						{
							title: '结清日期',
							key: 'clearDate',
							align: 'center',
							render: (h, params) => {
								if(params.row.repaymentSchedule == 'null'){
									return h('span', '-')
								}else {
									return h('span', params.row.repaymentSchedule.clearDate)
								}
							}
						},
						{
							title: '累计逾期天数',
							key: 'allOverdueDays',
							align: 'center',
							render: (h, params) => {
								if(params.row.repaymentSchedule == 'null'){
									return h('span', '-')
								}else {
									return h('span', params.row.repaymentSchedule.allOverdueDays)
								}
							}
						}
					],
					outLoanList: [],
					dateRange: [],
					totalPage: 10,
					searchParams: {
						loanId: '',
						aBaseName: '',
						startTime: '',
						endTime: '',
						currentPage: 1,
						pageSize: 10
					}
				},
				tab: 'outLoan',
			}
		},
		created: function () {
			this.loadList();
		},
		methods: {
			pageChange: function (val) {
				let _this = this;
				if (_this.outLoanTab.searchParams.currentPage != val) {
					_this.outLoanTab.searchParams.currentPage = val;
					_this.loadList();
				}
			},
			pageSizeChange: function (val) {
				let _this = this;
				if (_this.outLoanTab.searchParams.pageSize != val) {
					_this.outLoanTab.searchParams.pageSize = val;
					_this.loadList();
				}
			},
			loadList: function () {
				let _this = this;
				_this.$Message.config({
					top: 200,
					duration: 2
				});
				clearTimeout(timer);
				_this.$Message.destroy();
				let timer = setTimeout(function () {
					_this.$Message.loading({
						content: '数据请求中...',
						duration: 0
					});
				}, 500);
				this.$http.post('AfterLoan/ClearCreditCustomer', _this.outLoanTab.searchParams)
					.then(function (response) {
						let res = response.data;
						clearTimeout(timer);
						_this.$Message.destroy();
						if (res.code == 1) {//正常
							_this.$Message.success({
								content: res.msg
							});
							if(res.data.list){
								_this.outLoanTab.totalPage = res.data.count;
								_this.outLoanTab.searchParams.currentPage = res.data.currentPage;
								_this.outLoanTab.searchParams.pageSize = res.data.pageSize;
								_this.outLoanTab.outLoanList = res.data.list;
							}
						} else {
							_this.$Message.error(res.msg);
						}
					})
					.catch(function (error) {
						clearTimeout(timer);
						_this.$Message.destroy();
						_this.$Message.error({
							content: error
						})
					});
			},
			dateChange: function (range) {
				let _this = this;
				if (_.includes(range, undefined)) {
					_this.outLoanTab.searchParams.startTime = '';
					_this.outLoanTab.searchParams.endTime = '';
				} else {
					_this.outLoanTab.searchParams.startTime = range[0];
					_this.outLoanTab.searchParams.endTime = range[1];
				}
			},
			searchoutLoan: function () {
				let _this = this;
				//清空页码
				_this.outLoanTab.searchParams.currentPage = 1;
				_this.outLoanTab.searchParams.pageSize = 10;
				//搜索
				_this.loadList();
			},
			refresh: function () {
				this.loadList();
			},
			clearAll: function () {//清空搜索，翻页等信息
				this.outLoanTab.dateRange = [];
				this.outLoanTab.searchParams.loanId = '';
				this.outLoanTab.searchParams.aBaseName = '';
				this.outLoanTab.searchParams.startTime = '';
				this.outLoanTab.searchParams.endTime = '';
				this.outLoanTab.searchParams.currentPage = 1;
				this.outLoanTab.searchParams.pageSize = 10;
			},
		}
	}
</script>
<style>
	.tab {
		padding-top: 15px;
	}

	.ivu-tabs-tab {
		font-size: 16px;
	}

	.page {
		padding-top: 50px;
		display: flex;
		justify-content: flex-end;
		flex-direction: row;
		padding-bottom: 50px;
	}

	.layout-content .extra-btn {
		color: #20abff;
		font-size: 14px;
	}

	.layout-content .ivu-input-group-prepend {
		background-color: #20abff;
		border-color: #20abff;
		width: 80px;
	}

	.yellow-color .ivu-input-group-prepend {
		background-color: #f0b320;
		border-color: #f0b320;
		width: 80px;
	}

	.input-title {
		color: #fff;
		font-size: 14px;
	}

	.search-box {
		display: flex;
		flex-direction: row;
		justify-content: space-around;
		align-items: center;
		padding-top: 14px;
		padding-bottom: 15px;
		flex-wrap: wrap;
	}

	.search-item {
		flex: 1;
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: center;
		padding-bottom: 15px;
		margin-right: 10px;
	}

	.item-title {
		width: 80px;
		height: 36px;
		font-size: 14px;
		line-height: 36px;
		text-align: center;
		color: #fff;
		border-top-left-radius: 4px;
		border-bottom-left-radius: 4px;
	}

	.color-yellow {
		background-color: #f0b320;
	}

	.color-red {
		background-color: #ff7043;
	}

	.color-green {
		background-color: #28b779;
	}

	.search {
		width: 100px;
		margin-right: 10px;
	}

	.ivu-select-selection {
		border-top-left-radius: 0px;
		border-bottom-left-radius: 0px;
	}

	.search-item .ivu-input {
		border-top-left-radius: 0px;
		border-bottom-left-radius: 0px;
	}
</style>



